<template>
	<div class="index-warp">
		<h2>大魔王的料理店</h2>
		<p class="title">库存</p>
		<el-table :data="indexData" border style="width: 100%" ref="indexTable">
            <el-table-column type="index" label="序号"  width="250">
            </el-table-column>
			<el-table-column prop="customer" label="客户名称"  width="250">
            </el-table-column>
            <el-table-column prop="createdate" label="订单日期"  width="300">
            </el-table-column>
            <el-table-column prop="goodsName" label="产品名称" width="260">
            </el-table-column>
            <el-table-column prop="goodsQuantity" label="数量" width="150">
            </el-table-column>
            <el-table-column prop="goodsPrice" label="价格" width="150">
            </el-table-column>
            <el-table-column prop="readydate" label="交付日期">
            </el-table-column>
        </el-table>
        <div class="pagination">
           <!--  <el-pagination
                   layout="prev, pager, next"
                   :total="1000">
           </el-pagination> -->
        </div>
	</div>
</template>
<script>
import API from '../../api/API'
const api = new API();
export default{
  	data() {
        return {
        	tableData: [],
            cur_page: 1,
            select_word:''
        }
    },
 	beforeMount:function(){
 	let self = this;
    let params = {
            api:"/product/index/orderInfo",
            param:{
                type:'0',
                pagesNum:1
            }
        }
        api.post(params).then(function(res){
            self.tableData = res.data.data;
        })
        
    },
    methods:{
       
    },
    computed:{
    	indexData(){
    		const self = this;
		   	return self.tableData.filter(function(d){
		   		if(d.goodsName.indexOf(self.select_word)>-1){
		   			return d;
		   		}
		   	})
    	}
    },
}
</script>
<style lang="scss" scoped="" type="text/css">
	.index-warp{
		h2{
			color: red;
		}
		.title{
			text-align: left;
			font-size: 20px;
			display: inline-block;
			float: left;
		}
		.search{
			width: 300px;
			float: left;
			margin: 10px 0 0 40px;
		}
		.pagination{
			text-align: right;
			margin-top: 20px;
		}
	}
</style>


